
import React, { useEffect, useState } from 'react';
import {web3} from '../wallet/web3';
import abi from '../abi';

const Cards: React.FC = () => {
    const[balanceOf,setBalanceOf] = useState(0);
    const[accounts, setAccounts] = useState<string[]>();
    const[DividendToken,setDividendToken] = useState(0);
    const[Staking, setStaking] = useState(0);
    const[totalMembers,setTotalMembers] = useState(0);
    const[tDividend,setTotalDividend] = useState(0);
    const[value,setValue] = useState('');
    const[busd,setBusd] = useState(0);

 useEffect(() => {
    async function getData() {
      const response = await fetch(
        `https://min-api.cryptocompare.com/data/price?fsym=BUSD&tsyms=USD`
      )
      let busd = await response.json();
  
      console.log(busd.USD)
      setBusd(busd.USD); 
    }
    getData()
  }, [])

   

    useEffect(() => {
        const CardValue  = async () => {
            const accounts = await web3.eth.getAccounts();
            console.log(accounts[0])

            if(accounts.length >0) {
          const balanceOf = await abi.methods.balanceOf(accounts[0]).call();
          const DividendToken = await abi.methods.dividendTokenBalanceOf(accounts[0]).call();
         
          const Staking = await abi.methods.stakingAmounts(accounts[0]).call();
          const totalMembers = await abi.methods.getNumberOfDividendTokenHolders().call();
          const tDividend = await abi.methods.getTotalDividendsDistributed().call();

          setAccounts(accounts)
          setBalanceOf(balanceOf)
          setDividendToken(DividendToken)
          setStaking(Staking)
          setTotalMembers(totalMembers)
          setTotalDividend(tDividend)
          
        }
          
          

          // const CurrentPrice = web3.utils.fromWei(Price, 'ether');
    
         
        
         
          
        };
        CardValue();
    });
       


        const lockMyToken = async (e:any) => {
            e.preventDefault();
            const accounts = await web3.eth.getAccounts();
              await abi.methods.stake(value).send({
                from: accounts[0],
              });
              
              }

            const cLaim = async (e:any) => {
                e.preventDefault();
                const accounts = await web3.eth.getAccounts();
                await abi.methods.claim().send({
                    from: accounts[0],
                });
            }

            const ReInvest = async (e:any) => {
                e.preventDefault();
                const accounts = await web3.eth.getAccounts();
                await abi.methods.setReinvest(1).send({
                    from: accounts[0],
                })
            }
       


    return(
         <>
         <div style={{background:"#061d3d"}}>
             <br />
         <div className="container">
           <div className="row">
                  <div className="col-sm-4"> 
                   <div className="card">
                       <div className="badge bg-info"><h4>Dividends Share</h4></div>
                       <h3>{Number(DividendToken / 1e9).toFixed(2)}</h3>
                       <p>Your Dividends</p>
                   </div>
                    </div>
                  <div className="col-sm-4">
                  <div className="card">
                  <div className="badge bg-info">    <h4>Balance</h4></div>
                       <h3>{Number(balanceOf / 1e9).toFixed(2)}</h3>
                       <p>EBIRD</p>
                   </div>
                  </div>
                  <div className="col-sm-4">
                  <div className="card">
                  <div className="badge bg-info">   <h4>Dividends Paid</h4></div>
                       <h3>{Number(DividendToken / 1e9).toFixed(2)}</h3>
                       <p>EBIRD</p>
                   </div>
                  </div>
                  <div className="col-sm-4">
                  <div className="card">
                  <div className="badge bg-info">  <h4>UnPaid Dividends</h4></div>
                       <h3>{Staking / 1e9}</h3>
                       <p>BUSD</p>
                   </div>
                  </div>

                  <div className="col-sm-4">
                  <div className="card">
                  <div className="badge bg-info">    <h4>Last Payment</h4></div>
                       <h3>0</h3>
                       <p>EBIRD</p>
                   </div>
                  </div>

                  <div className="col-sm-4">
                  <div className="card">
                  <div className="badge bg-info">   <h4>Position</h4></div>
                       <h3>{totalMembers}</h3>
                       <p>Total Users</p>
                   </div>
                  </div>
                  </div>

            <br />
                 <div className="jumbotron">
                      <div className="col-sm-12">
                           
                            <div className="card">
                            <div className="badge bg-info">  <h2>Total Dividends Paid</h2></div>
                                 <h1>{ Number(tDividend / 1e18).toFixed(2)} BUSD</h1>
                                 <h3>
                              
                                      {tDividend * busd} USD</h3>
                            </div>
                      </div>
                       
                 </div>



                 <br />
                 <div className="jumbotron">
                      <div className="col-sm-12">
                           
                            <div className="card">
                            <div className="badge bg-info">  <h2>STAKING</h2></div> <br />
                                 <div className="col-xs-3"><form className="form-control" onSubmit={lockMyToken}>
                                      <input type="number" placeholder="Duration" value={value}
            onChange={(e) => setValue(e.target.value)} />
              <h2>Choose your stacking contract</h2>
                                 <button className="btn btn-primary">LOCK MY TOKENS</button>
                                       </form>
                                     
                                        </div>
                                     
                            </div>
                      </div>
                       
                 </div>



                 
                 <br />
                 <div className="jumbotron">
                      <div className="col-sm-12">
                           
                            <div className="card">
                            <div className="badge bg-info">  <h2>REWARDS</h2></div> <br />
                                <h3>CLAIM YOUR BUSD NOW</h3>
                                       <form onSubmit={cLaim}>
                                      <button className="btn btn-primary">CLAIM</button>
                                      </form>

                                 <p>Need at least 10 million EBIRD for automatic claim</p>
                             </div>
                      </div>
                       
                 </div>


                 <br />
                 <div className="jumbotron">
                      <div className="col-sm-12">
                           
                            <div className="card">
                            <div className="badge bg-info">  <h2>REVINVEST</h2></div> <br />
                                <h3>REINVEST YOUR TOKENS</h3>
                                       <form onSubmit={ReInvest}>
                                       
                                 <button className="btn btn-primary">REINVEST</button>
                                 </form>
                                
                             </div>
                      </div>
                       
                 </div>
                 <br /> <br />

                  </div>
                  </div>
         </>
    )
}

export default Cards;